$base: #D8D7C3;
$light: #7DB9B3;
$mid: #166678;
$dark: #0E2E3B;

.userPage {
	height: 100%;
	margin-top: 1%;
	background: $base;
}

.chatPage {
	height: 100%;
	margin-top: 1%;
	background: $mid;
}

.userList {
	background: #eee;
}

#msgList, .msgInput, .msgBtn {
	margin-top: 2px;
}

#msgList {
	padding-left: 10px;
}

#msgList li:nth-child(odd) {
	background: #eee;
}

.usernInput, .msgInput {
	height: 34px;
	border-width: 1px;
	border-radius: 4px;
}

#chatInput {
	position: fixed;
	bottom: 0;
	width: 100%;
	border-top: 2px solid;
	margin: 2px;
}